import { StarIcon } from '../../../icons';
import { Typography, HStack, VStack, Tag } from '../../../components';

export function ReviewView() {
  return (
    <VStack
      style={{
        background: 'white',
        marginTop: '10px',
        padding: '32px',
        border: '1px solid #b7b7b7',
        borderRadius: '20px',
        // flexWrap: 'wrap',
        gap: '24px',
      }}
    >
      <VStack
        gap="20px"
        style={{
          minWidth: '96px',
          alignItems: 'center',
          justifyContent: 'center',
          padding: '0px 16px 0px 0px',
          borderRight: '1px solid #b7b7b7',
          flexWrap: 'wrap',
        }}
      >
        <VStack
          gap="5px"
          style={{
            minWidth: 'fit-content',
            alignItems: 'center',
            fill: 'var(--accent)',
          }}
        >
          <StarIcon width="24px" height="24px" />
          <Typography variant="t1" children="5.0" />
        </VStack>
        <HStack gap="8px">
          <VStack gap="8px" style={{ justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="t6" children="" />
            <Tag children="" />
          </VStack>
          <VStack gap="8px" style={{ justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="t6" children="" />
            <Tag children="" />
          </VStack>
          <VStack gap="8px" style={{ justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="t6" children="" />
            <Tag children="" />
          </VStack>
          <VStack gap="8px" style={{ justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="t6" children="" />
            <Tag children="" />
          </VStack>
        </HStack>
      </VStack>
      <HStack gap="8px">
        {/* style={{ padding: '0px 0px 0px 16px' }} */}
        <VStack
          style={{
            overflow: 'hidden',
            height: 'min-content',
            flexWrap: 'wrap',
            gap: '4px',
          }}
        >
          {/* <span>   2024/04/19</span> */}
          <Tag children="" />
          <Tag children="" />
          <Tag children="" />
          <Tag children="" />
          <Tag children="" />
          <Tag children="" />
          <Tag children="" />
          <Tag children="" />
          <Tag children="" />
        </VStack>
        <Typography variant="t5" style={{ lineHeight: '150%' }}>
          <span style={{ color: 'grey' }}></span>       
            ! ..
        </Typography>
      </HStack>
    </VStack>
  );
}
