import { useNavigate } from 'react-router-dom';
import { useState } from 'react';
import qs from 'qs';

import { ignore } from '../../helper';
import { useSessionId } from '../../contexts/SessionIdContext';
import { VStack, HStack, Radio, A, Button, Input, TextField, Typography } from '../../components';
import { backend } from '../../api/backend';

export function LoginPage() {
  const [input, setInput] = useState({
    email: '',
    password: '',
  });

  const navigate = useNavigate();
  const { setSessionId } = useSessionId();

  const [saveLoginInfo, setSaveLoginInfo] = useState(false);
  const [loginError, setLoginError] = useState(false);

  function handleInput(event: React.ChangeEvent<HTMLInputElement>) {
    setInput({
      ...input,
      [event.target.id]: event.target.value,
    });
  }

  ignore(Input, loginError);

  async function handleLogin() {
    try {
      const response = await backend.post(
        '/api/login',
        qs.stringify({ email: input.email, password: input.password, 'remember-me': false }),
        {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
          },
        },
      );

      console.log(response.config);
      console.log(response.data);
      console.log(response.headers);
      console.log(response.request);
      console.log(response.status);
      console.log(response.statusText);

      if (response.status === 200) {
        window.alert(' .');
        setSessionId('sesssionId from backend server');
        navigate('/');
      }
    } catch (e) {
      window.alert(' .');
      setLoginError(true);
    }
  }

  return (
    <Typography
      variant="t5"
      style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '40px',
        height: '100%',
        width: '100%',
      }}
    >
      <HStack type="left" gap="48px" style={{ width: '400px' }}>
        <Typography bold variant="t1" style={{ textAlign: 'center' }}>
          
        </Typography>

        <form
          method="post"
          action="/login/api"
          onSubmit={(e) => {
            e.preventDefault();
            handleLogin();
          }}
        >
          <HStack gap="48px">
            <HStack gap="16px">
              <TextField
                required
                id="email"
                placeholder=" "
                onChange={handleInput}
                value={input.email}
                style={{ padding: '16px' }}
              />
              <TextField
                required
                id="password"
                type="password"
                placeholder=" "
                onChange={handleInput}
                errorMessage={loginError ? '     .' : ''}
                value={input.password}
                style={{ padding: '16px' }}
              />
              <VStack style={{ justifyContent: 'space-between', alignItems: 'center', padding: '16px' }}>
                <Radio
                  value={saveLoginInfo}
                  onClick={
                    (/* event: React.FormEvent<HTMLDivElement> */) => {
                      // event.stopPropagation();
                      setSaveLoginInfo(!saveLoginInfo);
                    }
                  }
                  label="  "
                />
                <A href="/login/find-password"> </A>
              </VStack>
            </HStack>

            <HStack style={{}} gap="20px">
              <Button
                type="submit"
                kind="filled"
                disabled={input.email === '' && input.password === ''}
                accnet="0"
                variant="contained"
                color="primary"
                style={{ padding: '16px', width: '100%' }}
              >
                <div></div>
              </Button>
              <Typography variant="t5" style={{ textAlign: 'center' }}>
                 ? <A href="/register"></A>
              </Typography>
            </HStack>
          </HStack>
        </form>
      </HStack>
    </Typography>
  );
}
