import { Link } from 'react-router-dom';

import { triggerWhenRendered } from '../contexts/RenderTriggerContext';
import { Button, HStack, Spacer, VStack, Typography } from '../components';

interface ErrorPageProps {
  code?: number | string;
}

// eslint-disable-next-line @typescript-eslint/no-unused-vars
export function ErrorPage(errorPageProps: ErrorPageProps) {
  triggerWhenRendered();
  return (
    <HStack gap="32px">
      <HStack gap="16px">
        <Typography variant="t1"> .</Typography>
        <Typography variant="t5">    .</Typography>
      </HStack>
      <VStack gap="16px">
        <Spacer />
        <Link to="/report">
          <Button></Button>
        </Link>
        <Link to="/">
          <Button> </Button>
        </Link>
      </VStack>
    </HStack>
  );
}

function ErrorLackOfPermissionPage({ code }: ErrorPageProps) {
  triggerWhenRendered();
  return (
    <HStack gap="32px">
      <HStack gap="16px">
        <Typography variant="t1"> .</Typography>
        <Typography variant="t5">  {code}   .</Typography>
      </HStack>
      <VStack gap="16px">
        <Spacer />
        <Link to="/login">
          <Button></Button>
        </Link>
        <Link to="/">
          <Button> </Button>
        </Link>
      </VStack>
    </HStack>
  );
}

export function ErrorLoginRequiredPage() {
  return <ErrorLackOfPermissionPage code=" " />;
}
